import React from 'react';
import './App.css';
import Hothot from './Hothot.js'
import HeartGenerator from './HeartGenerator.js'

class App extends React.Component {
  
  constructor(props) {
    super(props)
    this.state = {gray: true}
  }

  turnOnColor = () => {
    this.setState({gray: false})
  }

  render() {
    return (
      <div className="App">
        <header className="App-header" style={{filter: 'grayscale(100%)', animation: !this.state.gray && 'tocolor 2.1s forwards'}}>
          {!this.state.gray && <HeartGenerator/>}
          <Hothot turnOnColor={() => this.turnOnColor()}/>
        </header>
      </div>
    );
  }
}

export default App;
