import React from "react";
import heart1 from "./heart-filled.svg";
import heart2 from "./heart.svg";
import "./App.css";

const size = 240;
const width = (window.screen.width+size) * window.devicePixelRatio;
const height = (window.screen.height+size) * window.devicePixelRatio;

class Heart extends React.Component {
    constructor(props) {
        super();
        this.state = {
            left: Math.random() * width - size,
            top: Math.random() * height - size,
            scale: 0
        };
    }

    render() {
        return (
            <img
                className="heart"
                style={{
                    transform: `scale(${this.state.scale})`,
                    left: this.state.left,
                    top: this.state.top,
                    animationDuration: `${Math.random() * 3 + 2}s`,
                    animationDelay: `${Math.random() * -100}s`
                }}
                src={Math.random() < 0.8 ? heart1 : heart2}
            />
        );
    }
}

export default Heart;
