import React from 'react';
import hothot from './hothot.png'
import kiss from './kiss.svg'
import Kiss from './Kiss.js'
import './App.css';
import careless from './careless.ogg'
import kiss_sound from './kiss.mp3'
const song = new Audio(careless)
class Hothot extends React.Component {


    constructor(props) {
        super()
        this.state = { kisses: [], playing: false }
    }

    createKiss = async (e) => {
        this.props.turnOnColor()
        e.persist()
        if (!this.state.playing)
            song.play().then(() => {}, (error) => {console.error(error)})
        
        let kissSound = new Audio(kiss_sound)
        kissSound.volume = 0.3
        kissSound.play()
        this.setState((prevState) => ({kisses: [...prevState.kisses, <Kiss position={{x: e.clientX-16, y: e.clientY-16}}/>], playing: true}))
    }

    render() {
        return (
            <>
                <img className='hothot' onClick={this.createKiss.bind(this)} src={hothot} style={{zIndex: 1, transform: `scale(${0.5})`}}></img>
                <img className='hothot-glow' src={hothot} style={{zIndex: 0.5, transform: `scale(${0.5})`}}></img>    
                {this.state.kisses}
                <b className='love-counter'> Love Counter: {this.state.kisses.length} </b> 
            </>
        )
    }
}

export default Hothot;